import Vue from 'vue'
import Router from 'vue-router'
import TeamRoles from '@/components/TeamRoles'
import NotFound from '@/components/NotFound'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'TeamRoles',
      component: TeamRoles
    },
    {
      path: '/404',
      name: 'NotFound',
      component: NotFound
    }
  ]
})



