<template>
  <div id="belbin-matrix" v-if="teamRoles.filter(teamRole => teamRole.backstage == false).length != 0">
    <img class="role_icon" v-for="team_role in teamRoles" :key="'icon_' + team_role.id" :alt="team_role.name" :src="team_role.src" :class="{backstage: team_role.backstage}">
  </div>
  <div id="belbin-matrix" v-else>
    <img class="role_icon" v-for="team_role in teamRoles" :key="'icon_' + team_role.id" :alt="team_role.name" :src="team_role.src">
  </div>
</template>

<script>
export default {
  name: 'BelbinMatrix',
  props: ['teamRoles']
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#belbin-matrix {
  width: 180px;
  text-align: center;
  display: block;
  margin-left: auto;
  margin-right: auto;

  border: 5px solid #111;
  border-radius:25px;
  -moz-box-shadow: 35px 35px 7px #999999;
  -moz-border-bottom-right-radius: 15px;
  -moz-border-radius: 25px;
  -webkit-border-radius: 25px;
  padding: 20px;
  background-color: white;
}
</style>



