<template>
  <div class="homepage">
    <div class="hero-content">
      <div class="title" data-role="header">
        <h1>{{ title }}</h1>
      </div>
      <!-- <img class="9role_logo" src="../../static/logo.jpg"> -->

      <div class="head-line">
        <p>Diversity is our strength.</p>
        <p>Finding out your own Team Role preferences as well as those of your colleagues will enable you to ensure tasks are allocated to the appropriate team members and this will result in more a more effective and productive team.</p>
        <!-- <p>Team Role is all about how you behave and interact with others. Anything that causes your behavior to change can in turn have an impact on your Team Role preferences.</p> -->
        <p>Here is a test to help you figure out your Team Role preferences.</p>
      </div>
    </div>

    <div class="content">
      <matrix :teamRoles="team_roles"></matrix>
      <div id="role_questions">
        <!-- <ul data-role="listview" data-inset="true">
          <li class="role_question" v-for="(question, index) in shuffled_role_questions" :key="index" data-icon="false">
            <a href="javascript:void(0);" onclick="this.firstChild.click()">
              <input :id="'question_' + index" type="checkbox" v-model=question.checked data-role="none">
              <label :for="'question_' + index" class="role_question_checkbox" onclick="this.parentElement.firstChild.click()" v-on:click="tally(question.role)"></label>

              <div class="role_question_text">{{question.text}}</div>
            </a>
          </li>
        </ul> -->

        <div class="role_question" v-show="index == 0" v-for="(question, index) in shuffled_role_questions.filter(question => question.shown == false)" :key="index">
          <div class="right">
            {{question_counter + '/'+ role_questions.length}}
          </div>

          <fieldset class="ui-grid-a">
            <div class="ui-block-a"><input type="button" value="YES" v-on:click="swipeleft(question)"></div>
            <div class="ui-block-b"><input type="button" value="NO" v-on:click="swiperight(question)" data-theme="b"></div>
          </fieldset>

          <div class="role_question_text">{{question.text}}</div>
        </div>

        <div id="test-result" v-show="all_shown">
          <form action="https://formspree.io/jq@angelabor.com" method="POST">
            <!-- <matrix :teamRoles="team_roles"></matrix> -->
            <h3>Test Result</h3>

            <div class="test-result-text">
              <textarea name="Teammate Suggestions" v-model="suggestions" readonly="readonly" data-role="none" rows="7"></textarea>

              <div v-for="team_role in team_roles" v-if="!team_role.backstage" :key="'comment_' + team_role.id">
                <img class="role_icon result" :src="team_role.src" />
                <textarea :name="'Team Role: ' + team_role.name" v-model="team_role.comment" readonly="readonly" data-role="none" rows="7"></textarea>
              </div>

              <div v-for="team_role in team_roles" :key="'result_' + team_role.id">
                <input type="hidden" :name="team_role.name" :value="(!team_role.backstage ? 'Pass' : 'Miss') + ', Score: ' + team_role.tally + '/4'">
              </div>
            </div>

            <div class="email-data">
              <input type="hidden" name="_subject" value="9 Team Roles" />
              <input type="text" name="_cc" placeholder="What is your email?" />
              <input class="button" type="submit" value="Send" data-theme="b">
            </div>
            <input type="text" name="_gotcha" style="display:none" />
            <input type="hidden" name="_next" value="http://antslabor.com" />
          </form>
        </div>
      </div>

    </div>
    <br>
    <p>Let's make the world a better place!</p>
  </div>
</template>

<script>
import BelbinMatrix from './BelbinMatrix.vue'

export default {
  name: 'TeamRoles',
  components: {
    'matrix': BelbinMatrix
  },
  methods: {
    next: function (question) {
      question.shown = true
      this.question_counter += 1
    },
    swipeleft: function (question) {
      question.checked = true
      this.next(question)
    },
    swiperight: function (question) {
      question.checked = false
      this.next(question)
    },
    tally: function (role) {
      return this.role_questions.filter(question => (question.role === role && question.checked)).length
    },
    show: function (role, tally) {
      var roleInfo = this.team_roles.find(teamRole => teamRole.id === role)
      roleInfo.tally = tally
      roleInfo.backstage = tally < 3
    },
    get_comment: function (role, backstage) {
      var comment = this.comments.filter(comment => (comment.role === role))
      return backstage ? comment.miss : comment.have
    },
    shuffle: function (array) {
      var currentIndex = array.length
      var temporaryValue
      var randomIndex

      // While there remain elements to shuffle...
      while (currentIndex !== 0) {
        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex)
        currentIndex -= 1

        // And swap it with the current element.
        temporaryValue = array[currentIndex]
        array[currentIndex] = array[randomIndex]
        array[randomIndex] = temporaryValue
      }

      return array
    }
  },
  computed: {
    all_shown: function () {
      return (this.role_questions.filter(question => question.shown === false).length === 0)
    },
    suggestions: function () {
      var suggestion = 'Your ideal team member(s) would be: '
      var missingRoles = this.team_roles.filter(teamRole => teamRole.backstage === true)
      for (var i = 0; i < missingRoles.length; i++) {
        suggestion += '\n\n - ' + missingRoles[i].miss
      }
      return suggestion
    },
    shuffled_role_questions: function () {
      return this.shuffle(this.role_questions)
    },
    tally_innovator: function () {
      return this.tally('innovator')
    },
    tally_explorer: function () {
      return this.tally('explorer')
    },
    tally_analyst: function () {
      return this.tally('analyst')
    },
    tally_coordinator: function () {
      return this.tally('coordinator')
    },
    tally_implementer: function () {
      return this.tally('implementer')
    },
    tally_expert: function () {
      return this.tally('expert')
    },
    tally_driver: function () {
      return this.tally('driver')
    },
    tally_teammate: function () {
      return this.tally('teammate')
    },
    tally_proofreader: function () {
      return this.tally('proofreader')
    }
  },
  watch: {
    tally_innovator: function (tally) {
      this.show('innovator', tally)
    },
    tally_explorer: function (tally) {
      this.show('explorer', tally)
    },
    tally_analyst: function (tally) {
      this.show('analyst', tally)
    },
    tally_coordinator: function (tally) {
      this.show('coordinator', tally)
    },
    tally_implementer: function (tally) {
      this.show('implementer', tally)
    },
    tally_expert: function (tally) {
      this.show('expert', tally)
    },
    tally_driver: function (tally) {
      this.show('driver', tally)
    },
    tally_teammate: function (tally) {
      this.show('teammate', tally)
    },
    tally_proofreader: function (tally) {
      this.show('proofreader', tally)
    }
  },
  data () {
    return {
      title: '9 Belbin Team Roles',
      question_counter: 1,
      team_roles: [
        {
          id: 'innovator',
          name: 'Innovator',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Plant.jpg'),
          comment: 'You are the Innovator who generates new ideas and approaches to solve difficult problems. Innovators are absent-minded or forgetful. You thrive on praise but criticism is especially hard for you to deal with. Your ideas are so novel, which can be impractical at times. You may also be poor communicators and can tend to ignore given parameters and constraints.',
          miss: 'The Innovators who generate new ideas and approaches to solve difficult problems.'
        },
        {
          id: 'explorer',
          name: 'Explorer',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Resource-Investigator.jpg'),
          comment: 'You are the curious Explorer. You explore available options, develop contacts, and negotiate for resources on behalf of the team. You are enthusiastic team members, who identify and work with external stakeholders to help the team accomplish its objective. On the downside, you may lose enthusiasm quickly and are often overly optimistic. You might forget to follow up on a lead.',
          miss: 'The Explorers who search available options, develop contacts, and negotiate for resources on behalf of the team.'
        },
        {
          id: 'analyst',
          name: 'Analyst',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Monitor-Evaluator.jpg'),
          comment: 'You are the Analyst who is best at evaluating ideas that other people (often Innovators) come up with. You are shrewd and objective, and you carefully weigh the pros and cons of all the options before coming to a decision. You are critical thinkers and very strategic in your approach. You are often perceived as detached or unemotional. Sometimes you are poor motivators who react to events rather than instigating them.',
          miss: 'The Analysts who are best at evaluating ideas that other people (often Innovators) come up with.'
        },
        {
          id: 'implementer',
          name: 'Implementer',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Implementer.jpg'),
          comment: 'You are the Executive who gets things done. You turn the team\'s ideas and concepts into practical actions and plans. You are the person people can count on to get the job done. On the downside, you may be inflexible and can be somewhat resistant to relinquish your plans in favor of positive changes.',
          miss: 'The Implementers who turn the team\'s ideas and concepts into practical actions and plans.'
        },
        {
          id: 'coordinator',
          name: 'Coordinator',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Co-ordinator.jpg'),
          comment: 'You are the Leader who guides the team to the objectives. You are excellent listeners and you are naturally able to recognize the value that each team member brings to the table. You are calm and good-natured, and delegate tasks very effectively. On the downside, you may delegate away too much personal responsibility and may tend to be manipulative.',
          miss: 'The Coordinators who recognize the value that each team member brings to the table and guide the team to the objectives.'
        },
        {
          id: 'expert',
          name: 'Expert',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Specialist.jpg'),
          comment: 'You are the Expert who has specialized knowledge that is needed to get the job done. You pride yourself on your skills and abilities, and you work to maintain your professional status. Your job within the team is to be an expert in the area, and you commit yourself fully to your field of expertise. This may limit your contribution, and lead to a preoccupation with technicalities at the expense of the bigger picture.',
          miss: 'The Experts who have specialized knowledge that is needed to get the job done.'
        },

        {
          id: 'driver',
          name: 'Driver',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Shaper.jpg'),
          comment: 'You are the Driver who challenges the team to improve. You are dynamic and usually extroverted who enjoy stimulating others, questioning norms, and finding the best approaches for solving problems. You are the one who shakes things up to make sure that all possibilities are considered and that the team does not become complacent. You often see obstacles as exciting challenges and you tend to have the courage to push on when others feel like quitting. Your potential weaknesses may be that you are argumentative and that you may offend people\'s feelings.',
          miss: 'The Shapers who shake things up to make sure that all possibilities are considered and that the team does not become complacent.'
        },
        {
          id: 'teammate',
          name: 'Teammate',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Teamworker.jpg'),
          comment: 'You are the Active Listener who provides support and make sure that people within the team are working together effectively. You fill the role of negotiators within the team and you are flexible, diplomatic and perceptive. You are very popular and capable in your own right, but prioritize team cohesion and helping people get along. Your weaknesses may be a tendency to be indecisive and to maintain uncommitted positions during discussions and decision-making.',
          miss: 'The Teammates who provide support and make sure that people within the team are working together effectively.'
        },
        {
          id: 'proofreader',
          name: 'Closer',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Complete-Finisher.jpg'),
          comment: 'You are the Closer who sees projects getting completed thoroughly. You ensure there have been no errors or omissions and you pay attention to the smallest of details. You are described as perfectionists who are orderly, conscientious and anxious. Most effectively worked at the end of tasks to polish, perfect, and scrutinize the work for errors, subjecting it to the highest standards of quality control. However, you could be accused of taking your perfectionism to extremes. You worry unnecessarily and find it hard to delegate.',
          miss: 'The Proofreaders who ensure there have been no errors or omissions and you pay attention to the smallest of details.'
        }
      ],
      role_questions: [
        {shown: false, checked: false, role: 'innovator', text: 'Are you creative, imaginative, free-thinking? Are you a wellspring of original ideas?'},
        {shown: false, checked: false, role: 'innovator', text: 'Is it true that you are good at solving difficult problems in unconventional ways?'},
        {shown: false, checked: false, role: 'innovator', text: 'Is it true that you are introverted and prefer to work apart from the team?'},
        {shown: false, checked: false, role: 'innovator', text: 'Do you have a tendency to ignore less important things?'},

        {shown: false, checked: false, role: 'explorer', text: 'Is it true that you are outgoing, enthusiastic, communicative? Are you a salesperson who is the source of vitality?'},
        {shown: false, checked: false, role: 'explorer', text: 'Do you use your curiosity and inquisitive nature to discover ideas and bring back to the team? Do you explore opportunities and develops contacts?'},
        {shown: false, checked: false, role: 'explorer', text: 'Do you have a tendency to be optimistic? Are you easily diverted?'},
        {shown: false, checked: false, role: 'explorer', text: 'Are you extroverted, meaning that others are often receptive to you and your thoughts?'},

        {shown: false, checked: false, role: 'analyst', text: 'Are you sober, strategic, discerning, sees all options and judges accurately?'},
        {shown: false, checked: false, role: 'analyst', text: 'Can you provide a logical eye, making impartial judgments where required and weighs up the team\'s options in a dispassionate way?'},
        {shown: false, checked: false, role: 'analyst', text: 'Do you provide critique with the expectation of improving performance?'},
        {shown: false, checked: false, role: 'analyst', text: 'Do you have a tendency to make decisions slowly?'},

        {shown: false, checked: false, role: 'coordinator', text: 'Are you a mature, confident, excellent chairperson? Are you the decision maker and delegator?'},
        {shown: false, checked: false, role: 'coordinator', text: 'Do you take on the traditional team-leader role?'},
        {shown: false, checked: false, role: 'coordinator', text: 'Would you be able to identify talent and clarify goals?'},
        {shown: false, checked: false, role: 'coordinator', text: 'Do you focus on the team\'s objectives and draw out team members and delegate work appropriately?'},

        {shown: false, checked: false, role: 'implementer', text: 'Are you disciplined, reliable, conservative and efficient? Do you turn ideas into practical actions and organizes work that needs to be done?'},
        {shown: false, checked: false, role: 'implementer', text: 'Do you have a tendency to plan a workable strategy and carry it out as efficiently as possible?'},
        {shown: false, checked: false, role: 'implementer', text: 'Do you tend to resist unproven ideas? Are you somewhat inflexible and slow to respond to new possibilities?'},
        {shown: false, checked: false, role: 'implementer', text: 'Would you be able to work systematically and efficiently? Are you very well organized?'},

        {shown: false, checked: false, role: 'expert', text: 'Are you focused, self-starting and dedicated? Providing knowledge & skills in rare supply?'},
        {shown: false, checked: false, role: 'expert', text: 'Do you bring in-depth knowledge of a key area to the team?'},
        {shown: false, checked: false, role: 'expert', text: 'Do you have a tendency to overload people with information?'},
        {shown: false, checked: false, role: 'expert', text: 'Do you tend to overlook the “big picture”? Do you contribute only on a narrow front and dwell on the technicalities?'},

        {shown: false, checked: false, role: 'driver', text: 'Are you challenging, dynamic, thrives on pressure? Are you the courage to overcome obstacles, brings the competitive drive to the team?'},
        {shown: false, checked: false, role: 'driver', text: 'Do you provide the necessary drive to ensure that the team keeps moving and does not lose focus or momentum?'},
        {shown: false, checked: false, role: 'driver', text: 'Are you prone to provocation, impatience, and sometimes offend peoples’ feelings? Are you seen as abrasive?'},
        {shown: false, checked: false, role: 'driver', text: 'Can you risk becoming aggressive and bad humored in your attempts to get things done?'},

        {shown: false, checked: false, role: 'teammate', text: 'Are you co-operative, mild, perceptive and diplomatic? Do you help the team to gel?'},
        {shown: false, checked: false, role: 'teammate', text: 'Do you have the versatility to identify the work required and complete it on behalf of the team?'},
        {shown: false, checked: false, role: 'teammate', text: 'Are you indecisive in crunch situations and tends to avoid confrontation?'},
        {shown: false, checked: false, role: 'teammate', text: 'It is safe to say that you are hesitant to make unpopular decisions?'},

        {shown: false, checked: false, role: 'proofreader', text: 'Are you painstakingly conscientious, anxious? Do you seek out errors and omissions?'},
        {shown: false, checked: false, role: 'proofreader', text: 'Do you always meet deadlines and work to highest standards?'},
        {shown: false, checked: false, role: 'proofreader', text: 'Are you very concerned with deadlines and will push the team to make sure the job is completed on time?'},
        {shown: false, checked: false, role: 'proofreader', text: 'Is it true that you are a perfectionist?'}
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import '../assets/css/checkbox.css';

h1, h2 {
  font-weight: bold;
  margin: .6em 0 .8em !important;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
p {
  font-family: Helvetica, Arial, Sans-Serif;
}
p.highlight {
  font-weight: bold;
}

button, input.button {
  border: 2px solid blue;
  border-radius: 25px;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  float: right;
}

input[type=text] {
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

textarea {
  padding: 10px;
  width: 100%;
  min-height: 5em;
  max-height: 15em;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-family: Helvetica, Arial, Sans-Serif;
  font-size: 16px;
}

#topBtn {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  border: none;
  outline: none;
  background-color: red;
  color: white;
  cursor: pointer;
  padding: 15px;
  border-radius: 10px;
}

#topBtn:hover {
  background-color: #555;
}

div.head-line {
  width: 60%;
  text-align: left;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 10px;
}

div.content {
  padding: 0px 20px;
}

#role_questions{
  width: 80%;
  text-align: left;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  margin-top: 20px;

  border: 1px dashed #ddd;
  box-shadow: 0 0 0 3px #fff, 0 0 0 5px #ddd, 0 0 0 10px #fff, 0 0 2px 10px #eee;
  background-color: white;
}

.role_question {
  display: block;
  text-align: left;
  padding: 5px;
}

.role_question_text {
  display: inline-block;
  vertical-align: middle;
  white-space: normal;
  padding-left: 10px;
  padding-right: 40px;
  font-family: Helvetica, Arial, Sans-Serif;
}

.test-result-text {
  text-align: left;
}

</style>



